export const CompanyStructureLoader = ({ rows = 6 }: { rows?: number }) => {
  return (
    <Skeleton data-testid="companyStructureLoader">
      <SkeletonRow variant="heading" size="medium" />
      <SkeletonRow variant="empty" size="small" />
      {Array.from({ length: rows }).map((_, i) => (
        <SkeletonRow key={i} size="large" fullWidth={true} />
      ))}
    </Skeleton>
  );
};

export const CompanyUserFormLoader = () => {
  return (
    <Skeleton data-testid="companyUserFormLoader">
      <SkeletonRow variant="heading" size="medium" />
      <SkeletonRow variant="empty" size="small" />
      <SkeletonRow size="large" />
      <SkeletonRow size="large" />
      <SkeletonRow size="large" />
      <SkeletonRow size="large" />
      <SkeletonRow size="large" fullWidth={true} />
      <SkeletonRow size="large" />
      <SkeletonRow size="large" />
    </Skeleton>
  );
};

